import styled from 'styled-components';

export const Wrapper = styled.div`
    & .container-avatar {
        /* background: #6c757d59; */
       /*  border-radius: 50%; */
        width: 50px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    & .ant-avatar-lg {
        width: 50px!important;
        height: 52px!important;
    }
    & .ant-list-item-meta-avatar {
        margin-bottom: 20px !important;
    }
    & .ant-spin-container {
        padding-top: 20px;
        margin-top: 40px;
    }
    & .container-title {
        font-family: 'Jost';
        font-size: 22px;
        font-weight: 500
    }
    & .style-beneficio {
        margin-left: 87px;
    }

    ${({ itemLayout }) => {
        if (itemLayout == "vertical")
            return `
            & .ant-list-item-meta {
                display: contents!important;
            }
            & .container-avatar {
          /*       background: #6c757d59; */
                border-radius: 50%; 
                width: 50px;
                height: 50px;
                display: flex;
                justify-content: center;
                align-items: center;
            }
            `;
        if (itemLayout == "horizontal")
            return `
            & .ant-list-item-meta {
                display: flex;
                align-items: flex-start;
                align-items: center;
                justify-content: center;
       
            }
            `;
    }}
    
`;
export const WrapperReservationsList = styled.div`


    & .date-container{
        display:flex;
    }
    & .date-container > img{
        margin-right: 8px;
        height: 20px;
        width: 20px;
    }
    & .ant-list-split .ant-list-item {
        border-bottom: 1px solid #b1a8a8ba;
    }
    & .ant-col{
        padding:0px 10px!important;
    }
    & .ant-row-flex{
        width:100%;
    }
    & .status.aprobada .satus-indicator{
        background:#57EB7E;
    }
    & .status.esperando_aprobacion .satus-indicator{
        background:#43326B;
    }
    & .status.cancelada .status-indicador{
        background:#F14E64
    }
    & .status .satus-indicator{
        height:10px;
        width:10px;
        background:red;
        border-radius:50%;
        margin-right:4px;
        float: left;
        margin: 6px 4px;
    }
    & .date img {
        margin-right: 8px;
    }
    & .title {
        font-family: 'Jost';
        font-weight: 600;
        font-size: 20px;
        letter-spacing: -0.42px;
        color: #000000;
        opacity: 1;
        text-align: left;
    }
    & .date img {
        margin-right: 8px;
        width: 15px;
        height: 15px;
    }
    & .date {
        font-family: 'Jost';
        font-size: 17px;
        font-weight: 500;
        letter-spacing: 0px;
        color: #000000;
        opacity: 1;
    }
    & .prices {
        color:var(--color1);
        opacity: 1;
        font-family: 'Jost';
        font-weight: 600;
        letter-spacing: 0px;
        font-size: 17px;
    }
    & .status-name {
        font-family: 'Jost';
        font-weight: 400;
        font-size: 17px;
        letter-spacing: 0px;
        color: #000000;
        text-align: left;
        opacity: 1;
    }
    & .title a {
        color: rgba(0,0,0,.85);
    }
`;