import React, { useState, useEffect } from 'react';
import { WrapperHome } from "./Styles";
import Banner from '../../components/banner/Banner';
import { Row, Col, List } from "antd";
import { translate } from 'react-polyglot';
import { useSelector, useDispatch } from "react-redux";
import MyList from '../../components/my-list/MyList';
import CardsEntities from '../../components/card/CardsEntities';
import Loader from '../../components/loader/';
import { getService } from '../../services';
import { CHANGE_ENTITY } from '../../constants/actionTypes';
const data = [{
    title_es: "Como hacer un paquete?",
    title_en: "How to make a Pack?",
    description_es: "este paquete se hace de forma facil, solo debes  registrate para crear uno !vamos que esperas ",
    description_en: "This package is made easy, you just have to register to create one!",
    icon: "/images/hablar.svg"
},
{
    title_es: "Como hacer una reservación ?",
    title_en: "How to make a reservations?",
    description_es: "para hacer una reservación es sencillo y facil, solo debes escoger tu paquete y listo.",
    description_en: "To make a reservation is simple and easy, you just have to choose your package and that's it.",
    icon: "/images/globo.svg"
},
{
    title_es: "¡Ofertas, Ofertas, Ofertas!",
    title_en: "Offers, Offers, Offers!",
    description_es: "¿Buscas los mejores precios? ¡Encuentra las mejores ofertas en tus hoteles favoritos!",
    description_en: "Looking for the best prices? Find the best deals on your favorite hotels!",
    icon: "/images/etiqueta-de-precio.svg"
}
]
const HomeEntities = ({ t, entity_id, ...props }) => {
    const [loading, setLoading] = useState(true);
    const locale = useSelector(({ auth }) => {
        return auth.locale;
    });

    return (
        <WrapperHome>
            <Banner
                titlePosition="top"
                dataSource={[{
                    title: t("homeEntities.banner.title"),
                    //title: t("home.mostTopBanner.title"),
                    //description: t(""),
                    description: t("homeEntities.banner.description"),
                    image_path: "/images/santamarta-mag.jpg",
                    thubmnail: "/images/banner-min.png"
                }]} />
            <Row
                className="section-container-homeEntities"
                justify="center" type="flex" align="top" gutter={[{ xs: 8, sm: 16, md: 24 }, { xs: 8, sm: 16, md: 24 }]}>
                <Col span={24}>
                    <Col className="section section-container-title" xl={24} lg={24} md={24} xs={24}>
                        <div className="section-container">
                            <div className="section-title">
                                {/* arma tu paqute como quieras */}
                                <h2>{t("homeEntities.labelTitle")}</h2>
                            </div>
                            <div className="section-">
                            </div>
                        </div>
                    </Col>
                    <Col className="section section-list" xl={24} lg={24} md={24} xs={24}>
                        <div className="section-container section-list">
                            <MyList
                                itemLayout="vertical"
                                grid={props.grid || {
                                    gutter: 24,
                                    xl: 1,
                                    sm: 1,
                                    md: 1,
                                    lg: 2,
                                    xl: 3,
                      
                                  }}
                                renderItem={item => (<List.Item>
                                    <List.Item.Meta
                                        avatar={<div className="container-avatar"><img src={item.icon} /></div>}
                                        title={<div className="container-title"><span>{item[`title_${locale}`]}</span></div>}
                                        description={<div className="container-description">
                                            <span>{item[`description_${locale}`]}</span>
                                        </div>}
                                    />
                                </List.Item>)}
                                dataSource={data}
                            />
                        </div>
                    </Col>
                </Col>
            </Row>
            <Row
                className="section-container top-packs"
                justify="center" type="flex" align="top" gutter={20}>
                <Col span={24}>
                    <Col className="section section-container-entities" xl={24} lg={24} md={24} xs={24}>
                        <div className="container-entities">
                            <div className="section-title">
                                <h2>{t("homeEntities.labelTitleHome")}</h2>
                            </div>
                        </div>
                    </Col>
                    <Col className="section section-container-card" xl={24} lg={24} md={24} xs={24}>
                        <CardsEntities
                            reference="entities"
                        />
                    </Col>
                </Col>
            </Row>
        </WrapperHome>
    )
};
export default translate()(HomeEntities);