import styled from 'styled-components';

export const Wrapper = styled.div`

    & .section-container{
        margin:0px!important;
    }
    & .section-category{
        text-align: center;
        margin: 0px auto;
       /*  background:url(/images/colombia.png); */
    }
    & .full-width > div{
        background-color: #Fff!important;
    }
    & .full-width{
        padding:0px!important;
    }
    &  .top-packs{
        background: url(/images/colombia.png);
        background-repeat: no-repeat;
        background-position: right bottom;
        background-size: contain;
    }
    & .new-packs{
        background: url(/images/bg1.jpg);
        background-repeat: no-repeat;
        background-position: bottom!important;
    }
    & .offers-packs{
        background: url(/images/bg2.jpg);
        background-repeat: no-repeat;
        background-position: bottom!important;
    }
    
    & .section-container .section{
        margin:20px 0px;
    }
    & .section-container .section-headline{
        margin:0px!important;
    }
    & .section{
        padding: 0px!important;
    }
    & .section > .ant-divider{
        margin-bottom: 40px!important;
    }
    & .section .ant-divider::after,
    .section .ant-divider::before
    {
        background: url(/images/wave.png)!important;
        height: 20px!important;
        background-repeat-y: no-repeat !important;
        border: 0px!important;
        top: -4px!important;
    }
    & .section .ant-divider .ant-divider-inner-text{
        font-size: 24px!important;
        text-transform: uppercase!important;
        letter-spacing: 4.4px!important;

        font-family: 'Jost';
        font-weight: 600;

    }
   
   
    /* fvargas */
   /* .categories .category-item{
        cursor: pointer;
   }
   .categories .category-item-active{
        background-color:var(--color-primary)!important;
        color:#FFF!important;
   }
   .categories > *{
       margin:0px 8px;
   }
   .categories .ant-avatar > img {
        padding: 4px!important;
    } */
}
@media(max-width:576px){
    .section-container{
       /*  padding-right: 51px!important; */
    }
    .section .ant-divider .ant-divider-inner-text {
        font-size: 12px!important;
    }
}
${({ configuration }) => {
    return `
    border:${configuration && configuration.primary_color || "2px solid var(--color-primary)"}!important;
    color:${configuration && configuration.primary_color || "var(--color-primary)"}!important;
    `;
  }}
`;

export const WrapperHome = styled.div`

    & .section-container{
        display: flex;
        justify-content: center;
        align-items: center;
        padding:26px!important;
        text-align: center!important;
    }
    & .section-container .section-title h2 {
        font-size: 28px!important;
        font-weight: 600;
      /*   background:red; */
    }
    /* SECTION LIST */

   
    & .ant-spin-container {
        padding-top: 0px!important;
        margin-top: 0px!important;
    }
    & .ant-list-item-meta-avatar {
        margin-bottom: 27px !important;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    & .container-avatar {
        background: #be5cff5e!important;
        width: 124px!important;
        height: 120px!important;
       
    }
    & .container-avatar >img {
        width: 80px;
    }
    & .ant-list-item-meta-title {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    & .container-title {
        font-size: 22px;
        font-weight: 600!important;
        margin-bottom: 6px!important;
    }
    & .container-description{
      
        font-size: 20px;
        text-transform: capitalize;
        font-weight: 500;
    }

/* entities cards */

    & .section-container-entities{
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
    }
    & .container-entities .section-title h2 {
        font-size: 28px!important;
        font-weight: 600;
`;