const MyList = ({ t, reference, refinement, filterDefaultValues = {}, item, ...props }) => {
  const locale = useSelector(({ auth }) => {
    return auth.locale;
  });
  const [loading, setLoading] = useState(true);
  const [dataSource, setDataSource] = useState([]);
  const defaultRenderItem = (record, index) => {
    let item = record;
    if (refinement) item = refinement(record);
    let image = item.gallery ? item.gallery.gallery_media && item.gallery.gallery_media[0] &&
      item.gallery.gallery_media[0].path ?
      `${URL_S3}/${item.gallery.gallery_media[0].path}` : DEFAULT_IMAGE
      : DEFAULT_IMAGE;

    return (<List.Item>
      <List.Item.Meta
        avatar={<div className="container-avatar">
          <Avatar shape="square" size="large" src={image} />
        </div>}
        title={<div className="container-title"><span>{item[`name_${locale}`]}</span></div>}
        description={<div className="container-description"><span dangerouslySetInnerHTML={{ __html: item[`description_${locale}`] }} /></div>}
      // description={<div className="container-description"><span>{item[`description_${locale}`]}</span></div>}
      />
    </List.Item>)
  }
  const getData = () => {
    if (reference) {
      const service = getService(reference);
      setLoading(true);
      service.find({
        query: {
          ...filterDefaultValues
        }
      })
        .then(({ data }) => {
          setLoading(false);
          setDataSource(data);
        })
        .catch(err => {
          setLoading(false);
          message.error(err.message)
        })
    }
  }
  useEffect(() => {
    if (props.dataSource)
      setDataSource(props.dataSource)
  }, [props.dataSource])
  useEffect(() => {
    getData();
  }, [reference, filterDefaultValues]);
  return (
    <Wrapper itemLayout={props.itemLayout}>
      <Row type="flex" aligntop="center" justify="center" gutter="10" >
        <Col xl={24}>
          <List
            itemLayout={props.itemLayout || "horizontal"}
            grid={props.grid || {
              gutter: 24,
              xl: 1,
              sm: 1,
              md: 1,
              lg: 2,
              xl: 2,

            }}
            dataSource={dataSource}
            renderItem={props.renderItem || defaultRenderItem}
          />
        </Col>
      </Row>
    </Wrapper>
  );
};
export default translate()(MyList);