import React, { useEffect, useState } from 'react';
import { Card, Row, Col, Button, Avatar, Carousel, message } from "antd";
import { WrapperCarousel, WrapperCardEntity } from "./Styles";
import { getService } from "../../services/";
import { translate } from 'react-polyglot';
import { navigate } from "@reach/router"
import { useSelector, useDispatch } from "react-redux";
import { isMobile } from "react-device-detect";
import { DEFAULT_IMAGE, IMAGE_MAP_PIN, URL_S3 } from '../../constants';
import { CHANGE_ENTITY } from '../../constants/actionTypes';
let settings = {
  dots: true,
  infinite: false,
  speed: 500,
  slidesToShow: 4,
  slidesToScroll: 4,
  initialSlide: 0,
  responsive: [
    {
      breakpoint: 1024,
      settings: {
        slidesToShow: 3,
        slidesToScroll: 3,
        infinite: true,
        dots: true
      }
    },
    {
      breakpoint: 600,
      settings: {
        slidesToShow: 2,
        slidesToScroll: 2,
        initialSlide: 2
      }
    },
    {
      breakpoint: 480,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1
      }
    }
  ]
};
const { Meta } = Card;
const Item = ({ t, record, ...props }) => {
  const locale = useSelector(({ auth }) => {
    return auth.locale;
  });
  const dispatch = useDispatch();
  let { logo, name, description_es, city, country, cell_phone } = record;
  let cover = record.gallery ? record.gallery.gallery_media && record.gallery.gallery_media[0] &&
    record.gallery.gallery_media[0].path ?
    `${URL_S3}/${record.gallery.gallery_media[0].path}` : DEFAULT_IMAGE
    : DEFAULT_IMAGE;
  let photos = record.gallery && record.gallery.gallery_media ? record.gallery.gallery_media.length : 0;

  const handleClick = () => {
    dispatch({
      type: CHANGE_ENTITY,
      entity: record
    })
    navigate(`/packs/${record.id}/`);
  }
  return (
    <WrapperCardEntity>
      <Card
        style={{ height: 300 }}
        onClick={handleClick}
        cover={
          <div className={`image-container ${photos == 0 ? "empty" : ""}`}>
            <img
              className="card-image"
              src={cover}
            />
            {logo && <div className="logo">
              <Avatar shape="circle" size="large" src={`${URL_S3}/${logo}`} />
            </div>}
          </div>
        }
      >
        <Meta title={
          <div className="section-title">
            <div className="title">
              <h2>{name}</h2>
            </div>
            <div className="location">
              <Avatar shape="square" size="small" src={IMAGE_MAP_PIN} />
              {city && <div className="city">
                {city.name}
              </div>}
              {city && city.country && <div className="country">{city.country.name}</div>}
            </div>
          </div>
        }
          description={
            <div className="address-container">
              <div className="address">
                <span>
                  {record[`address_${locale}`]}
                </span>
              </div>
            </div>
          } />
      </Card>

    </WrapperCardEntity>
  )
}
const CardsEntities = ({ t, reference, onLoad, filterDefaultValues = {}, item, ...props }) => {
  const [dataSource, setDataSource] = useState(props.dataSource || [])
  const [loading, setLoading] = useState(false);
  const getData = () => {
    if (reference) {
      const service = getService(reference);
      setLoading(true);
      service.find({
        query: {
          ...filterDefaultValues
        }
      })
        .then(({ data }) => {
          setDataSource(data);
          setLoading(false);
          if (onLoad) onLoad();
        })
        .catch(err => {
          setLoading(false);
          message.error(err.message);
        })
    }

  }
  useEffect(() => {
    if (props.dataSource)
      setDataSource(props.dataSource)
    else
      getData();
  }, [props.dataSource]);
  return (
    <WrapperCarousel>
      <Row type="flex" aligntop="center" justify="center" gutter="10">
        <Col span="20">
          <Carousel {...settings} autoplay={props.autoplay || false}>
            {
              dataSource.map((item, index) => (<Item key={index} record={item} />))
            }
          </Carousel>
          {/* <Col xl={24}>
            <div className="button-show">
              <Button active className="mybutton">
                {t("packs.showAllText")}
              </Button></div>
          </Col> */}
        </Col>
      </Row>

    </WrapperCarousel >
  )
};
export default translate()(CardsEntities);